<template>
  <div class="page-renew">

    <mt-header title="我要续订">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="box check-info">
      <div class="check-date"  @click.stop="handleDate">
        <div class="check-in">入住日期 <span>7月01日</span></div>
        <div class="check-day">共29天</div>
        <div class="check-out">退房日期 <span>7月29日</span></div>
      </div>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleRenew" size="large" type="primary">提交订单</mt-button>
    </div>
    
  </div>
</template>

<script>

export default {
  name: 'page-renew',

  components: {
  },

  methods: {
    handleDate () {
      this.$router.push({ path: 'date', query: { refer: 'post' }})
    },
    handleRenew () {
      this.$messagebox({
        title: '档期已满',
        message: '您的房间<span>7月10日<\/span>至<span>7月14日<\/span>已经被人预定走喽，请修改时间',
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '不续订了',
        cancelButtonText: '修改时间',
      }).then(action => {
        if(action == "confirm"){
          window.history.back();
        }
      });

    }
  }
};
</script>



